.van-tag {
  position: relative;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 0.17067rem;
  padding: var(--tag-padding, 0 0.17067rem);
  color: #fff;
  color: var(--tag-text-color, #fff);
  font-size: 0.512rem;
  font-size: var(--tag-font-size, 0.512rem);
  line-height: 0.68267rem;
  line-height: var(--tag-line-height, 0.68267rem);
  border-radius: 0.08533rem;
  border-radius: var(--tag-border-radius, 0.08533rem);
}
.van-tag--default {
  background-color: #969799;
  background-color: var(--tag-default-color, #969799);
}
.van-tag--default.van-tag--plain {
  color: #969799;
  color: var(--tag-default-color, #969799);
}
.van-tag--danger {
  background-color: #ee0a24;
  background-color: var(--tag-danger-color, #ee0a24);
}
.van-tag--danger.van-tag--plain {
  color: #ee0a24;
  color: var(--tag-danger-color, #ee0a24);
}
.van-tag--primary {
  background-color: #1989fa;
  background-color: var(--tag-primary-color, #1989fa);
}
.van-tag--primary.van-tag--plain {
  color: #1989fa;
  color: var(--tag-primary-color, #1989fa);
}
.van-tag--success {
  background-color: #07c160;
  background-color: var(--tag-success-color, #07c160);
}
.van-tag--success.van-tag--plain {
  color: #07c160;
  color: var(--tag-success-color, #07c160);
}
.van-tag--warning {
  background-color: #ff976a;
  background-color: var(--tag-warning-color, #ff976a);
}
.van-tag--warning.van-tag--plain {
  color: #ff976a;
  color: var(--tag-warning-color, #ff976a);
}
.van-tag--plain {
  background-color: #fff;
  background-color: var(--tag-plain-background-color, #fff);
}
.van-tag--plain::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border: 0.04267rem solid currentColor;
  border-radius: inherit;
  content: '';
  pointer-events: none;
}
.van-tag--medium {
  padding: 0.08533rem 0.256rem;
  padding: var(--tag-medium-padding, 0.08533rem 0.256rem);
}
.van-tag--large {
  padding: 0.17067rem 0.34133rem;
  padding: var(--tag-large-padding, 0.17067rem 0.34133rem);
  font-size: 0.59733rem;
  font-size: var(--tag-large-font-size, 0.59733rem);
  border-radius: 0.17067rem;
  border-radius: var(--tag-large-border-radius, 0.17067rem);
}
.van-tag--mark {
  border-radius: 0 21.312rem 21.312rem 0;
  border-radius: 0 var(--tag-round-border-radius, 21.312rem) var(--tag-round-border-radius, 21.312rem) 0;
}
.van-tag--mark::after {
  display: block;
  width: 0.08533rem;
  content: '';
}
.van-tag--round {
  border-radius: 21.312rem;
  border-radius: var(--tag-round-border-radius, 21.312rem);
}
.van-tag__close {
  min-width: 1em;
  margin-left: 0.08533rem;
}
/* stylelint-disable selector-type-no-unknown, selector-max-type */
/**
 * Entry of basic styles
 */
.van-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.van-multi-ellipsis--l2 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-multi-ellipsis--l3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-clearfix::after {
  display: table;
  clear: both;
  content: '';
}
.van-hairline,
.van-hairline--top,
.van-hairline--left,
.van-hairline--right,
.van-hairline--bottom,
.van-hairline--top-bottom,
.van-hairline--surround {
  position: relative;
}
.van-hairline::after,
.van-hairline--top::after,
.van-hairline--left::after,
.van-hairline--right::after,
.van-hairline--bottom::after,
.van-hairline--top-bottom::after,
.van-hairline--surround::after {
  position: absolute;
  box-sizing: border-box;
  -webkit-transform-origin: center;
          transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  content: ' ';
  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid #ebedf0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.van-hairline--top::after {
  /* prettier-ignore */
  border-top-width: 1PX;
}
.van-hairline--left::after {
  /* prettier-ignore */
  border-left-width: 1PX;
}
.van-hairline--right::after {
  /* prettier-ignore */
  border-right-width: 1PX;
}
.van-hairline--bottom::after {
  /* prettier-ignore */
  border-bottom-width: 1PX;
}
.van-hairline--top-bottom::after {
  /* prettier-ignore */
  border-width: 1PX 0;
}
.van-hairline--surround::after {
  /* prettier-ignore */
  border-width: 1PX;
}
html,
body,
page {
  font-size: 0.59733rem;
}
